import React from 'react';
import styled from 'styled-components';

const HomeContainer = styled.div`
  text-align: center;
  padding: 2rem;
`;

const Title = styled.h1`
  font-size: 2.5rem;
  margin-bottom: 1rem;
`;

const Subtitle = styled.p`
  font-size: 1.2rem;
  color: #666;
`;

function Home() {
  return (
    <HomeContainer>
      <Title> YouYan3D </Title>
      <Subtitle> 3D </Subtitle>
    </HomeContainer>
  );
}

export default Home;

